import React,{useState} from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import MailIcon from '@material-ui/icons/Mail';
import WalletIcon from '@material-ui/icons/AccountBalanceWallet';
import SwapIcon from '@material-ui/icons/SwapHoriz';
import SettingsIcon from '@material-ui/icons/Settings';
import CartIcon from '@material-ui/icons/ShoppingCart';
import DashboardIcon from '@material-ui/icons/Dashboard';
import InputIcon from '@material-ui/icons/Input';



import { Redirect } from "react-router-dom"

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
    
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    marginTop:68,
  }
});

function SideDrawer(props) {
  const { classes } = props;

  const [path,setPath] = useState(null);

  const updatePath = (path) => {
    if(props.route !== path){
      setPath(path)
    }
  }
console.log(props)
  return (
    <div className={classes.root}>
    {path && <Redirect to={path} />}
      <CssBaseline />
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
        
      >
        
        <List>
          {/* {['Manage Wallet', 'Exchanges', 'Settings', 'Ventor Settings','Offline Paper Wallet', 'Logout'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))} */}
          
          <ListItem button onClick={()=>updatePath("/")}>
              <ListItemIcon><WalletIcon/></ListItemIcon>
              <ListItemText primary="Manage Wallet"/>
            </ListItem>
            <ListItem button onClick={()=>updatePath("/exchange")}>
              <ListItemIcon><SwapIcon/></ListItemIcon>
              <ListItemText primary="Exchanges"/>
            </ListItem>
            <ListItem button onClick={()=>updatePath("/settings")}> 
              <ListItemIcon><SettingsIcon/></ListItemIcon>
              <ListItemText primary="Settings"/>
            </ListItem>
            <ListItem button>
              <ListItemIcon><CartIcon/></ListItemIcon>
              <ListItemText primary="Ventor Settings"/>
            </ListItem>
            <ListItem button>
              <ListItemIcon><DashboardIcon/></ListItemIcon>
              <ListItemText primary="Offline Paper Wallet"/>
            </ListItem>
            <ListItem button>
              <ListItemIcon><InputIcon/></ListItemIcon>
              <ListItemText primary="Logout"/>
            </ListItem>
        </List>
      </Drawer>
    </div>
  );
}

SideDrawer.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SideDrawer);
